
'use client';

import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import Image from 'next/image';
import Link from 'next/link';
import { BookOpen, LoaderCircle, Search } from 'lucide-react';

import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from '@/components/ui/card';
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormMessage,
} from '@/components/ui/form';
import { searchYoutubeVideos, SearchYoutubeVideosInput, SearchYoutubeVideosOutput } from '@/ai/flows/search-youtube-videos';

const formSchema = z.object({
  query: z.string().min(2, 'Please enter a search term.'),
});

type Video = SearchYoutubeVideosOutput['videos'][0];

export default function ResourcesPage() {
  const [videos, setVideos] = useState<Video[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const form = useForm<z.infer<typeof formSchema>>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      query: '',
    },
  });

  async function onSubmit(values: z.infer<typeof formSchema>) {
    setIsLoading(true);
    setError(null);
    setVideos([]);
    try {
      const result = await searchYoutubeVideos(values as SearchYoutubeVideosInput);
      if (result.videos.length === 0) {
        setError('No videos found for your query. Try a different search term.');
      }
      setVideos(result.videos);
    } catch (err) {
        console.error('Error searching videos:', err);
        const errorMessage = (err as Error).message;
        if (errorMessage.includes('YOUTUBE_API_KEY')) {
            setError('The YouTube API Key is not configured. Please add it to your .env file.');
        } else {
            setError('An error occurred while fetching videos. Please try again.');
        }
    } finally {
      setIsLoading(false);
    }
  }

  return (
    <div className="p-4 md:p-8 space-y-6">
      <div className="flex items-center gap-4">
        <div className="p-3 bg-primary/10 rounded-lg">
          <BookOpen className="w-8 h-8 text-primary" />
        </div>
        <div>
          <h1 className="text-3xl font-bold tracking-tight font-headline">
            Learning Resources
          </h1>
          <p className="text-muted-foreground">
            Find courses, articles, and videos to help you grow.
          </p>
        </div>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Search for Learning Videos</CardTitle>
          <CardDescription>
            Enter a topic or skill to find relevant videos from YouTube.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <Form {...form}>
            <form onSubmit={form.handleSubmit(onSubmit)} className="flex items-start gap-4">
              <FormField
                control={form.control}
                name="query"
                render={({ field }) => (
                  <FormItem className="flex-grow">
                    <FormControl>
                      <div className="relative">
                        <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-5 w-5 text-muted-foreground" />
                        <Input placeholder="e.g., 'React hooks tutorial' or 'Data structures in Python'" className="pl-10" {...field} />
                      </div>
                    </FormControl>
                    <FormMessage className="mt-2" />
                  </FormItem>
                )}
              />
              <Button type="submit" disabled={isLoading} className="w-32">
                {isLoading ? (
                  <LoaderCircle className="mr-2 h-4 w-4 animate-spin" />
                ) : (
                  'Search'
                )}
              </Button>
            </form>
          </Form>
        </CardContent>
      </Card>

      {isLoading && (
         <div className="text-center py-20">
            <LoaderCircle className="w-12 h-12 text-primary animate-spin mx-auto" />
            <p className="mt-4 text-muted-foreground">Searching for videos...</p>
        </div>
      )}

      {error && (
        <div className="text-center py-20">
          <p className="text-destructive">{error}</p>
        </div>
      )}

      {!isLoading && videos.length > 0 && (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {videos.map((video) => (
            <Card key={video.videoId} className="overflow-hidden transition-all hover:shadow-xl hover:-translate-y-1">
              <Link href={`https://www.youtube.com/watch?v=${video.videoId}`} target="_blank" rel="noopener noreferrer" className="group">
                <div className="relative aspect-video">
                  <Image
                    src={video.thumbnailUrl}
                    alt={video.title}
                    fill
                    className="object-cover"
                  />
                </div>
                <CardHeader>
                  <CardTitle className="text-base font-semibold group-hover:text-primary transition-colors">{video.title}</CardTitle>
                  <CardDescription className="text-xs pt-1">{video.channelTitle}</CardDescription>
                </CardHeader>
              </Link>
            </Card>
          ))}
        </div>
      )}

    </div>
  );
}
