// src/ai/flows/search-youtube-videos.ts
'use server';

/**
 * @fileOverview A flow for searching YouTube videos.
 *
 * - searchYoutubeVideos - A function that searches YouTube for videos based on a query.
 * - SearchYoutubeVideosInput - The input type for the searchYoutubeVideos function.
 * - SearchYoutubeVideosOutput - The return type for the searchYoutubeVideos function.
 */

import { ai } from '@/ai/genkit';
import { z } from 'genkit';
import { GoogleAuth } from 'google-auth-library';

const SearchYoutubeVideosInputSchema = z.object({
  query: z.string().describe('The search query for YouTube.'),
});
export type SearchYoutubeVideosInput = z.infer<typeof SearchYoutubeVideosInputSchema>;

const VideoSchema = z.object({
    videoId: z.string(),
    title: z.string(),
    description: z.string(),
    thumbnailUrl: z.string(),
    channelTitle: z.string(),
});

const SearchYoutubeVideosOutputSchema = z.object({
  videos: z.array(VideoSchema).describe('A list of YouTube videos matching the query.'),
});
export type SearchYoutubeVideosOutput = z.infer<typeof SearchYoutubeVideosOutputSchema>;

async function fetchYoutubeVideos(query: string): Promise<SearchYoutubeVideosOutput> {
    const apiKey = process.env.YOUTUBE_API_KEY;
    if (!apiKey) {
        throw new Error('YOUTUBE_API_KEY is not set in the environment variables.');
    }
    
    const url = `https://www.googleapis.com/youtube/v3/search?part=snippet&q=${encodeURIComponent(query)}&type=video&maxResults=9&key=${apiKey}`;

    try {
        const response = await fetch(url);
        if (!response.ok) {
            const errorBody = await response.json();
            console.error('YouTube API Error:', errorBody);
            throw new Error(`YouTube API request failed with status ${response.status}`);
        }
        const data = await response.json();
        const videos = data.items.map((item: any) => ({
            videoId: item.id.videoId,
            title: item.snippet.title,
            description: item.snippet.description,
            thumbnailUrl: item.snippet.thumbnails.high.url,
            channelTitle: item.snippet.channelTitle,
        }));

        return { videos };
    } catch (error) {
        console.error('Failed to fetch YouTube videos:', error);
        throw error;
    }
}

const searchYoutube = ai.defineTool(
    {
      name: 'searchYoutube',
      description: 'Searches YouTube for videos based on a query.',
      inputSchema: SearchYoutubeVideosInputSchema,
      outputSchema: SearchYoutubeVideosOutputSchema,
    },
    async (input) => {
      return await fetchYoutubeVideos(input.query);
    }
);

const searchYoutubeVideosFlow = ai.defineFlow(
  {
    name: 'searchYoutubeVideosFlow',
    inputSchema: SearchYoutubeVideosInputSchema,
    outputSchema: SearchYoutubeVideosOutputSchema,
  },
  async (input) => {
    const searchResult = await searchYoutube(input);
    return searchResult;
  }
);


export async function searchYoutubeVideos(input: SearchYoutubeVideosInput): Promise<SearchYoutubeVideosOutput> {
  return searchYoutubeVideosFlow(input);
}
